import { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import "./Form.scss"
import { dataRetrievals } from '../../actionCreators'
import Button from '../Button'

class Form extends PureComponent {
  state = {
    dataRetrieval: [],
    isShowMore: false,
    date: {},
  };
  componentDidMount = async () => {
    try {
      const response = await fetch('https://www.nbrb.by/api/exrates/rates?periodicity=0');
      const dataset = await response.json();
      const { dataRetrievals } = this.props;
      dataRetrievals(dataset);
      this.setState({ date: dataset[0].Date });
      this.setState({ isShowMore: true });
    } catch (err) {
      return this.setState({ isShowMore: false });
    }
  }
  // handleClick = async () => {
  //   this.setState({ isShowMore: !this.state.isShowMore });
  //   try {
  //     const response = await fetch('https://www.nbrb.by/api/exrates/rates?periodicity=0');
  //     const dataset = await response.json();
  //     const { dataRetrievals } = this.props;
  //     dataRetrievals(dataset);
  //   } catch (err) {
  //   }
  // }

  render() {
    const { dataRetrieval } = this.props;
    const { isShowMore, date } = this.state;
    return (
      <section className="form">
        <Link to="/setting" className="form__setting">Настройки</Link>
        {isShowMore &&
          <div>
            {/* <Button text={isShowMore ? "Обновить" : "Показать"} onClick={this.handleClick} /> */}
            <div className='form__date'>Дата:{date.split('T')[0]}</div>
            <table border="1" className="dataset">
              <tr>
                <th>Буквенный <br /> код <br /> валюты</th>
                <th>Текущий <br /> курс <br /> валют</th>
                <th> Копировать <br /> в буффер <br /> обмена</th>
              </tr>
              {dataRetrieval.map((item) => (
                <tr>
                  <td>{item.Cur_Abbreviation}</td>
                  <td>{item.Cur_OfficialRate}</td>
                  <td><button onClick={() => navigator.clipboard.writeText(item.Cur_OfficialRate)}>{item.Cur_Abbreviation}</button></td>
                </tr>
              ))}
            </table>
          </div>
        }
      </section>
    )
  }
}
const mapStateToProps = ({ dataRetrieval }) => ({
  dataRetrieval: dataRetrieval.dataRetrieval,
});
const mapDispatchToProps = (dispatch) => ({
  dataRetrievals: (dataset) => dispatch(dataRetrievals(dataset)),
});
export default connect(mapStateToProps, mapDispatchToProps)(Form);
